.section-about {
  background-color: $color-grey-light;
  padding: 20rem 0;
  margin-top: -10vh;
}

.section-features {
  padding: 20rem 1rem;
  background-image: linear-gradient(
      to right bottom,
      rgba($color-primary-light, 0.8),
      rgba($color-primary-dark, 0.8)
    ),
    url("./img/nat-4.jpg");
  background-size: cover;
  background-position: top;

  transform: skewY(-7deg);

  & > * {
    transform: skewY(7deg);
  }
}

.section-tours {
  background-color: $color-grey-light;
  margin-top: -10vh;
  padding: 25rem 0;
}

.section-stories {
  position: relative;
  background-color: transparent;
  padding: 10rem 0;
}

.section-book {
  padding: 10rem 0;
  background-image: linear-gradient(
    to right bottom,
    $color-primary,
    $color-primary-light
  );
}

.book {
  background-image: linear-gradient(
      105deg,
      rgba($color-white, 0.8) 0%,
      rgba($color-white, 0.8) 50%,
      rgba($color-white, 0) 50%
    ),
    url("../img/nat-10.jpg");
  background-size: cover;
  box-shadow: 0.3rem 3rem 4rem rgba($color: $color-black, $alpha: 0.3);
  border-radius: 0.5rem;

  &__form {
    width: 50%;
    padding: 6rem;
  }
}
